<script>
	import Header from './components/Header.svelte';
	import Footer from './components/Footer.svelte';
	import Search from './components/Search.svelte';
	import Card from './components/Card.svelte';
	import Loader from './components/Loader.svelte';
	import Weather from './components/Weather.svelte';
	import Time from './components/Time.svelte';
	import Location from './components/Location.svelte';
	import Currency from './components/Currency.svelte';
	import Language from './components/Language.svelte';
	$:link="https://en.wikipedia.org/wiki/"+search;
	let show=false;
	$: search='';
	$: search_code="";
	let accu_key='o8lkJuEjhlMXiH8EiHt8Z6F7AxQSJJ17';
	let locationdata;
	let timezone;
	$:worlddata='';
	$:flag='';
   

    const worldapi=async()=>{
	  
	  let country=locationdata['Country']['EnglishName'];
	  if (country=='United States')
	  {
		  country="United States of America";
	  }
	  else if(country=='South Korea')
	  {
		  country='Republic%20of%20Korea';
	  }
	  else if(country=='Vietnam')
	  {
		  country='viet nam';
	  }
	  else if(country=='Russia')
	  {
		  country='Russian Federation';
	  }
	  const response = await fetch(`https://restcountries.eu/rest/v2/name/${country}?fullText=true`);
	  worlddata= await response.json();
	  return worlddata[0];
	 
	 }
            
	const gotSearch=(e)=>{
		worlddata='';
		flag='';
		search=e.detail;
		show=false;
		getaccudetails(search).then(data=>{
			locationdata=data;
			show=true;
			if(data===undefined) 
			{
				search_code="";
			}
			else{
				
				search_code=data['Key'];
				timezone=data['TimeZone']['Name']
			}
			worldapi().then(data=>{
               flag=data['flag'];
			}).catch(err=>{
				console.log(err)
			});

		}
		).catch(err=>{
			show=true;
			console.log(err)
		})
	}
	const getaccudetails=async (search)=>{
		let base='https://dataservice.accuweather.com/locations/v1/cities/search';
		let query=`?apikey=${accu_key}&q=${search}`;
		const response = await fetch(base+query);
		let data= await response.json();
		data=data[0];
		return data;
		
	}
	
</script>


<Header>
Any City Dets
</Header>

<main>
	<Search holder="search a city" on:gotSearch={gotSearch}>
	</Search>

<div class="tot_wrapper">
	{#if search!=''}
	{#if show==true}
		{#if search_code==''}
		    <h6>
			Seems like there's no city named {search}
			</h6>
		    
		{:else}
            <h6 class="heading">
			Search results related to {search}.
			</h6>
		
			<div class="row">
				<div class="col l4 s12 offset-l1">
				<div class="row">
					<div class="col l12 s12">
						<Card cardtitle="Time and Date">
						<Time {timezone} />
						</Card>
					</div>
					<div class="col l12 s12">
						<Card cardtitle="Location">
						<Location data={locationdata} {flag}/>
						</Card>
					</div>
				</div>
				</div>

				<div class="col l5 s12 offset-l2">
				<Weather {accu_key} {search_code}/>
				</div>
			</div>

			{#if worlddata!=''}
				<div class="row">
				<div class="col l4 s12 offset-l1">
				<Card cardtitle="Currency">
				<Currency {worlddata} />
				</Card>
				</div>

				<div class="col l4 s12 offset-l2">
				<Card cardtitle="Languages">
				<Language {worlddata} {locationdata} />
				</Card>
				</div>
				</div>
	    	{/if}

			<div class="btn white wiki">
			<a href={link} target="_blank" style="text-decoration:none;heigth:fit-content;">wikipedia of {search}</a>
			</div>

		{/if}
	{:else}
	  <Loader color="brown"/>
	{/if}
	{/if}
</div>
		   
</main>

<Footer source="./img/city.svg"/>

<style>
.wiki{
	margin:20px auto -50px auto;
}
.tot_wrapper{
	margin:30px auto 0px auto;
	width:85%;
}
.heading{
	margin:25px;
	font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
	color: rgb(179, 179, 179);
}
	main {
		text-align: center;
		padding: 1em;
		width: 80%;
		margin:40px auto;
	}


	@media (min-width: 640px) {
		main {
			max-width: none;
		}
	}
	@media (max-width: 640px) {
	.tot_wrapper{
	margin:30px auto 0px auto;
	width:90%;
}
	}
</style>